.header {
  background-color: $color1;
  display: flex;
  justify-content: space-around;
  align-items: center;

  .logo {
    transition-duration: ease-in-out 0.2s;
    @extend .py-5;
    img {
      height: 80px;
    }
  }

  .logo:hover {
    transform: scale(1.2);
  }

  .navBar {
    display: flex;
    a {
      color: #fff;
    }

    li:hover {
      @extend .hover;
    }
  }
}

