$color1: #aa0601;
$color2: #fbf2f2;

$fontColor1: #650300;

$mt: mt !default;
$mb: mb !default;
$ml: ml !default;
$mr: mr !default;
$mx: mx !default;
$my: my !default;
@for $i from 1 through 30 {
  .#{$mt}-#{$i} {
    margin-top: $i * 4px;
  }

  .#{$mb}-#{$i} {
    margin-bottom: $i * 4px;
  }

  .#{$ml}-#{$i} {
    margin-left: $i * 4px;
  }

  .#{$mr}-#{$i} {
    margin-right: $i * 4px;
  }

  .#{$mx}-#{$i} {
    margin-left: $i * 4px;
    margin-right: $i * 4px;
  }

  .#{$mr}-#{$i} {
    margin-top: $i * 4px;
    margin-bottom: $i * 4px;
  }
}

$pt: pt !default;
$pb: pb !default;
$pl: pl !default;
$pr: pr !default;
$px: px !default;
$py: py !default;
@for $i from 1 through 30 {
  .#{$pt}-#{$i} {
    padding-top: $i * 4px;
  }

  .#{$pb}-#{$i} {
    padding-bottom: $i * 4px;
  }

  .#{$pl}-#{$i} {
    padding-left: $i * 4px;
  }

  .#{$pr}-#{$i} {
    padding-right: $i * 4px;
  }

  .#{$px}-#{$i} {
    padding-left: $i * 4px;
    padding-right: $i * 4px;
  }

  .#{$py}-#{$i} {
    padding-top: $i * 4px;
    padding-bottom: $i * 4px;
  }
}

.hover {
  transform: scale(1.2);
  transition: ease-in-out 0.2s;
}
